@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  padding: 0 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 17px;
  overflow: hidden;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .container {
    min-width: 768px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.divider {
  display: block;
  margin: 0 -30px;
}

.modal-close {
  display: none;
}

.popover-close {
  display: none;
}

.popover {
  height: 395px;
  width: 320px;
}

.popover__list {
  list-style-type: none;
}

.popover__link {
  background: #ffffff;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}

.header {
  display: flex;
  color: #000000;
  background-color: #ffffff;
  width: 100%;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .header {
    min-height: 88px;
  }
}
@media (min-width: 1150px) {
  .header {
    min-height: 88px;
  }
}

.logo__icon:hover {
  opacity: 0.6;
}
.logo__icon:disabled {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .logo__mobile {
    display: none;
  }
}
.logo__tablet {
  display: none;
}

@media (min-width: 768px) {
  .logo__tablet {
    display: block;
  }
}
@media (min-width: 1150px) {
  .logo__tablet {
    display: none;
  }
}
.logo__desktop {
  display: none;
}

@media (min-width: 1150px) {
  .logo__desktop {
    display: block;
  }
}
.navigation {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .navigation {
    height: 88px;
    position: relative;
  }
  .navigation::after {
    content: "";
    position: absolute;
    top: 88px;
    left: -50px;
    min-height: 1px;
    width: 100vw;
    background-color: #000000;
    opacity: 0.1;
    z-index: 99;
  }
}
@media (min-width: 1150px) {
  .navigation {
    height: 88px;
    width: 100%;
  }
  .navigation::after {
    content: "";
    position: absolute;
    top: 88px;
    left: -50px;
    min-height: 1px;
    width: 100vw;
    background-color: #000000;
    opacity: 0.1;
    z-index: 99;
  }
}

.navigation__container {
  display: flex;
  min-width: 260px;
  align-items: center;
  min-height: 79px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__container {
    min-width: 668px;
  }
}
@media (min-width: 1150px) {
  .navigation__container {
    width: 100%;
  }
}

.navigation__logo {
  margin: 22px 0 22px 84px;
  height: 35px;
  text-align: center;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__logo {
    margin: 22px 0 20px 0;
    height: 46px;
    order: 1;
  }
}
@media (min-width: 1150px) {
  .navigation__logo {
    margin: 0 auto;
    height: 65px;
    z-index: 99;
  }
}

.navigation__logo img {
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__logo img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .navigation__logo img {
    width: 149px;
    height: 72px;
  }
}
.navigation__logo img:hover {
  opacity: 0.6;
}
.navigation__logo img:disabled {
  opacity: 0.3;
}

.navigation__menu {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}
@media (min-width: 1150px) {
  .navigation__menu {
    flex-direction: row;
  }
}

.menu__button {
  margin-left: auto;
  padding: 10px;
  border: none;
  background-color: #ffffff;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .menu__button {
    display: none;
  }
}
@media (min-width: 1150px) {
  .menu__button {
    display: none;
  }
}

.menu__icon-close {
  display: block;
}

.menu__icon-open {
  display: none;
}

@media (min-width: 320px) and (max-width: 768px) {
  .menu__nojs .menu__button {
    display: none;
  }

  .menu__nojs .menu__wrapper {
    position: static;
    display: flex;
    min-height: 0;
    margin: 0 -30px;
  }

  .menu__nojs .menu__item {
    display: block;
  }
}
.menu__wrapper {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 79px;
  right: -30px;
  z-index: 2;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
  min-width: 320px;
  width: 100vw;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .menu__wrapper {
    position: relative;
    flex-direction: row;
    margin: 0;
  }
}
@media (min-width: 1150px) {
  .menu__wrapper {
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    height: 88px;
  }
}

.menu__links {
  display: flex;
  flex-direction: column;
  list-style: none;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .menu__links {
    flex-direction: row;
    position: absolute;
    top: -86px;
    left: -20px;
  }
}
@media (min-width: 1150px) {
  .menu__links {
    flex-direction: row;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .menu__links .menu__item:first-child {
    margin-right: 45px;
  }
}
@media (min-width: 1150px) {
  .menu__links .menu__item:first-child {
    margin-right: 35px;
    width: 184px;
    border-right: 1px solid rgba(0, 0, 0, 0.2);
  }
}

.menu__buttons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style: none;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .menu__buttons {
    position: absolute;
    top: -167px;
    right: 114px;
    flex-direction: row;
  }
}
@media (min-width: 1150px) {
  .menu__buttons {
    flex-direction: row;
  }
}

@media (min-width: 1150px) {
  .menu__buttons .menu__item:last-child {
    padding-left: 79px;
    width: 100%;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .menu__buttons .menu__item:first-child {
    margin-right: 79px;
    height: 88px;
    width: 82px;
  }
}
@media (min-width: 1150px) {
  .menu__buttons .menu__item:first-child {
    min-width: 82px;
  }
}

.menu__link {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 79px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
  width: 100%;
  min-height: 79px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .menu__link {
    padding: 0;
    border: none;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .menu__link {
    padding: 0;
    border: none;
    height: 100%;
  }
}

.menu__links .menu__link {
  font-size: 17px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .menu__links .menu__link {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .menu__links .menu__link {
    font-size: 17px;
  }
}

.menu__item:hover .menu__link {
  color: rgba(0, 0, 0, 0.6);
}

.menu__item:hover .menu__link svg {
  fill: #63d1bb;
}

.search {
  position: relative;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .search {
    border-left: 1px solid rgba(0, 0, 0, 0.2);
