  align-items: center;
  margin: 0 -30px;
  background-color: #f1f1f1;
  height: 160px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 143px;
    margin: 0 -50px;
