}
@media (min-width: 768px) and (max-width: 1149px) {
  .catalog__hero {
    margin: 0 -50px;
    position: relative;
    min-height: 226px;
  }
  .catalog__hero::after {
    content: "";
    position: absolute;
    bottom: -12px;
    z-index: 99;
    left: 0;
    width: 100vw;
    background: url("../img/background/zigzag-hero.png") no-repeat;
    background-size: 100% 100%;
    min-height: 24px;
  }
}
@media (min-width: 1150px) {
  .catalog__hero {
    margin: 0;
    position: relative;
    min-height: 255px;
