  .catalog__hero::after {
    content: "";
    position: absolute;
    bottom: -12px;
    z-index: 99;
    left: 0;
    width: 100%;
    background: url("../img/background/zigzag-hero.png") no-repeat;
    background-size: 100% 100%;
    min-height: 24px;
  }
}

.catalog__fresh {
